import { createUserWithEmailAndPassword, updateProfile } from '@firebase/auth';
import React, { Component, useContext, useState } from 'react'
import { Link } from 'react-router-dom';
import Loader from '../../component/Loader/Loader';
import { LoaderContext } from '../../context/loaderContext';
import { auth, googlePopup } from '../../firebase/firebase';


const Register = () =>  {
    const [loader, setLoader] = useContext(LoaderContext);
const [state, setState] = useState({
    displayName:'',
    email:'',
    password:'',
    conPassword:'',
    loading:false
})
    const handleChange = (e) =>{
        const {name , value} =e.target;
        setState({[name]:value})
    }
   const  handleSubmit = (e) =>{
        e.preventDefault();
        setLoader(true)
        const {email, password, conPassword, displayName} = state;
        if(password === conPassword){
            createUserWithEmailAndPassword(auth, email, password)
            .then(user => {
                console.log('new USer', user);
                updateProfile(auth.currentUser, {displayName}).finally(done =>  setLoader(false))
            });
        } else{
            alert('check your password again')
        }
        
    }
  
        return (
            <div className='form'>
                  <h1>Register Here</h1>
           <form onSubmit={handleSubmit}>
               <input type="text" name='displayName' placeholder='User Name' onChange={handleChange} value={state.displayName} required/>
               <input type="email" name='email' placeholder=' enter your your Email' onChange={handleChange} value={state.email} required/>
               <input type="password" name='password' placeholder='Enter PassWord' onChange={handleChange} value={state.password} required/>
               <br />
               (make sure is password atleast 6 character long)
               <input type="password" name='conPassword' placeholder='Confirm Password' onChange={handleChange} value={state.conPassword} required/>
               <button>Submit</button>
           </form>
           <button  className="google" onClick={googlePopup}>Sign In With Google</button>
           <h3>Already an User? <Link to='/login'>click Here</Link></h3>
            </div>
        )
    
}
export default Register;